import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';

const MovieDeatil = () => {
  let { id } = useParams();
  const [movieData, setmovieData] = useState([]);
  const [apiKey] = useState(process.env.REACT_APP_API);
  const [company, setCompany] = useState([]);
  const [genres, setGenres] = useState([]);
  const request = () => {
    axios
      .get(`https://api.themoviedb.org/3/movie/${id}?api_key=${apiKey}`)
      .then((res) => {
        setmovieData(res.data);
        setCompany(res.data.production_companies);
        setGenres(res.data.genres);
      });
  };
  useEffect(() => {
    request();
  }, []);

  console.log(movieData);
  return (
    <div className='container deatilContainer'>
      <div className='row'>
        <div className='col s12 m6 l5'>
          <img
            src={`https://image.tmdb.org/t/p/w500/${movieData.poster_path}`}
            alt='Movie Poster'
            width='100%'
          />
        </div>
        <div className='col s12 m6 l7'>
          <h3
            style={{
              padding: '0px !important',
              margin: '5px 0',
            }}
          >
            <strong>
              {movieData.title ? movieData.title : movieData.name}
            </strong>
          </h3>
          <br />
          <h6 style={{ display: 'inline', fontSize: '1.6em' }}>
            <strong>Overview : </strong>
          </h6>
          <p
            style={{
              display: 'inline',
              fontSize: '1.4em',
              lineHeight: '10px',
              letterSpacing: '1px',
            }}
          >
            {movieData.overview}
          </p>
          <br />

          <h6>
            <div className='genres'>
              <strong>Genres : </strong>
              {genres.map((genres, i) => (
                <span key={i}>
                  <strong>{genres.name}</strong>,{' '}
                </span>
              ))}
            </div>
            <br />

            <div className='released_date'>
              <strong>Date : </strong>
              {movieData.release_date}
            </div>
            <br />

            <div className='popularity'>
              <strong>Popularity : </strong>
              {movieData.popularity}
            </div>
            <br />

            <div className='status'>
              <strong>Status : </strong>
              {movieData.status}
            </div>
            <br />

            <div className='language'>
              <strong>Language : </strong>
              {movieData.original_language}
            </div>
            <br />

            <div className='company'>
              <strong style={{ display: 'block', paddingBottom: '5px' }}>
                Production Company :{' '}
              </strong>

              {company.map((comp) => (
                <li key={comp.id} style={{ padding: '5px 40px' }}>
                  {comp.name}
                </li>
              ))}
            </div>
          </h6>
        </div>
      </div>
    </div>
  );
};

export default MovieDeatil;
